import React from 'react';
import { Scrollbars } from 'react-custom-scrollbars';

import './Offer.css';

const range = [
  'uzyskania wiadectwa bezpieczestwa przemysowego',
  'uzyskania akredytacji bezpieczestwa teleinformatycznego',
  'opracowania i wdroenia polityki bezpieczestwa',
  'opracowania i implementacji planu ochrony',
  'szkolenia z zakresu ochrony danych osobowych i informacji niejawnych'
]

const Offer = () => (
  <Scrollbars
    renderThumbHorizontal={() => <div className="thumb thumb--horizontal"/>}
    renderThumbVertical={() => <div className="thumb thumb--vertical"/>}
    className="content__overlay offer"
    style={{ position: window.innerWidth <= 768 ? 'fixed' : 'absolute' }}
  >
    <div className="inner">
      <h3>Oferta</h3>
      <div>
        <p>
          Nasz specjalnoci jest wiadczenie usug konsultingowych i doradczych zwizanych z ochron informacji niejawnych, danych osobowych oraz tajemnicy przedsibiorstwa. Oferujemy usugi w zakresie doboru zabezpiecze fizycznych, elektronicznych i bezpieczestwa systemw teleinformatycznych.
        </p>
        <hr />
        <p>Udzielamy wsparcia w zakresie:</p>
        <div className="offer__list">
          {range.map((title, key) => (
            <div
              key={key}
              className="offer__list-item"
            >
              {title}
            </div>
          ))}
        </div>
        <p>wiadczymy usugi zwizane z utrzymaniem i instalacj systemw bezpieczestwa.</p>
      </div>
    </div>
  </Scrollbars>
);

export default Offer;



